<template>
  <RouterView :table="table" @setMasterTable="setMasterTable"/>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      table: null
    }
  },
  methods: {
    setMasterTable(table) {
      this.table = table;
      sessionStorage.setItem('table', this.table);
    }
  }
}
</script>
