export default async function(url = '', m = 'GET', data) {

  const opt = {
    method: m,
    headers: {
      "Content-Type": "application/json"
    }
  }

  if(data) {
    if(data instanceof FormData) {
      console.log(data);
      opt.body = data;
    } else {
      opt.body = JSON.stringify(data);
    }
  }

  const response = await fetch(url, opt);
  return response;
}
