import { createRouter, createWebHistory } from 'vue-router';
import HomeField from '../views/HomeField.vue';

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeField
    },
    {
      path: '/order',
      name: 'orderMaster',
      component: () => import('../UserMaster.vue'),
      children: [
        {
          path: 'main',
          name: 'OrderList',
          component: () => import('../views/OrderList.vue'),
          props: true
        },
        {
          path: 'cart',
          name: 'CartField',
          component: () => import('../views/CartField.vue'),
          props: true
        },
        {
          path: 'hist',
          name: 'UserHistory',
          component: () => import('../views/UserHistory.vue'),
          props: true
        },
        {
          path: 'accounting',
          name: 'UserAccounting',
          component: () => import('../views/UserAccounting.vue')
        }
      ]
    },
    {
      path: '/master',
      name: 'regiMaster',
      component: () => import('../RegiMaster.vue'),
      children: [
        {
          path: 'regi',
          name: 'Regi',
          component: () => import('../views/Regi/TableRegi.vue'),
        },
        {
          path: 'category',
          component: () => import('../views/Regi/CategoryList.vue'),
          props: true
        },
        {
          path: 'merchandice',
          name: 'ItemList',
          component: () => import('../views/Regi/ItemList.vue'),
          props: true
        },
        {
          path: 'table',
          name: 'TableLayout',
          component: () => import('../views/Regi/TableLayout.vue'),
          props: true
        },
        {
          path: 'history',
          name: 'OrderHistory',
          component: () => import('../views/Regi/OrderHistory.vue'),
          props: true
        },
      ]
    },
    {
      path: '/kitchen',
      name: 'KitchenMaster',
      component: () => import('../KitchenMaster.vue'),
      children: [
        {
          path:'home',
          name:'HomeView',
          component: () => import('../views/Kitchen/HomeView.vue')
        },
        {
          path:'order',
          name: 'KitchenOrder',
          component: () => import('../views/Kitchen/KitchenOrderView.vue')
        }
        ,{
          path:'recommend',
          name: 'RecommentdationField',
          component: () => import('../views/Kitchen/RecommendField.vue')
        }
      ]
    },
    {
      path: '/:pathMatch(.*)*',
      name: 'ErrorWindow',
      component: () => import('../views/ErrorWindow.vue')
    }
  ]
});

export default router
