<template>
    <section class="welcomeMessage">
        <h2 class="welcome-ttl">Welcome To OneOrder</h2>
        <p class="welcome-desc">
            OneOrder は 居酒屋向けを前提としたモバイルオーダーシステムです。<br><br>
            手違いでの注文で問題が発生した場合や<br>
            未成年の飲酒については OneOrder は一切の責任を負いません。<br>

            (適当な説明文です)
        </p>
        <p class="error-text"></p>
        <p class="welcome-ok">以上に問題がない方はOKを押してください。</p>
        <RouterLink class="welcome-btn" to="order/main">OK</RouterLink>
    </section>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name: 'HomeField',
    data() {
        return {
            table: null
        }
    },
    mounted() {
        const params = new URL(window.location.href).searchParams;
        if(params.size > 0 || sessionStorage.getItem('table')) {
            // 多分params.get('tb') は一意に識別できる別の値(暗号化されてたり)になるかもしれないし、ただのテーブルidになるかもしれないし、もしかしたらテーブル名になるかもしれない
            if(params.get('tb')) {
                const dat = params.get('tb');
                Ajax(`/api/table/status/${dat}`)
                .then(res => res.json())
                .then(dat => {

                    if(dat.error) {
                        const welcomeBtn = document.querySelector('.welcome-btn');
                        welcomeBtn.remove();
                        const h2 = document.createElement('h2');
                        h2.textContent = 'このテーブルは現在使用できません。';
                        h2.className = 'Unwelcome-msg';
                        this.$el.appendChild(h2);
                    } else if(dat.status === 'お会計待ち') {
                        const welcomeBtn = document.querySelector('.welcome-btn');
                        welcomeBtn.remove();
                        const h2 = document.createElement('h2');
                        h2.textContent = 'このテーブルはお会計待ちの状態です。';
                        h2.className = 'Unwelcome-msg';
                        this.$el.appendChild(h2);
                    } else if(dat.status === '来店済み' || dat.status === '空き状態') {
                        this.table = dat.table;
                        this.$emit('setMasterTable', this.table);
                    }
                });
            }
        } else {
            const desc = this.$el.querySelector('.error-text');
            desc.innerHTML += 'テーブル情報が設定されていないため、このまま進めても注文はできません。<br>操作方法がわからない場合はホールスタッフを呼んでください。';
        }
    },
    emits: [
        'setMasterTable'
    ]
}
</script>
